
.text-left{
    padding-left: 5rem;
    text-align: left;
}

.drawing-area{
    max-width: 1000px;
    height: 700px;
    /* background-color: lightgray; */
    background-color: white;
    border: 2px solid #badfe7;
    margin: auto;
}

.save-button{
    background-color:#6fb3b8;
}

.btn-style1{
    padding:10px; 
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 20px;
}
.btn-style2{
    padding-left:10px;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 20px;
}

.Heading-about{
    
    color: #388087
}


.about-page{
    max-width: 800px;
    margin: auto;
    width: 50%;
    padding: 10px;
    font-size: 28px;
    background-color: white;
    border-radius: 20px;
}

.word-imagin{
    color: #6fb3b8;
}

.word-reality{
    color: #6fb3b8;
}


.about-footer{
    position: fixed;
    margin: auto;
    bottom: 0;
    width: 100%;
}

.a-tag{
    color: #388087
}

.about-img{
    width: 100%;
    max-width: 700px;
    height: auto;
}

#controllers {
    width: 600px;
    font-size: 0.8em;
    color: #333;
  }
  #paint {
    border: 1px solid #000;
    background: #fff;
  }
  .controller {
    display: inline-block;
    background: #eee;
    border: 1px solid #e7e7e7;
    padding: 5px;
  }

.footer-btn{
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    gap: 30px;
}